.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.cardsu{
  display:flex;
  flex-direction: row;
  justify-content:space-evenly;
  padding: 10px;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header{
  background-color: #022338;
  border-radius: 10px;
  color:#fff;
  text-align: center;
}
.body{
  margin: 10px;
  font-size: 2rem;
  text-align: center;
  justify-content: center;
  align-items: center;
  align-self: center;
}
.img{
  margin:0px 25%;
  height: 500px;
  width: 600px;
}
